import SearchBar from './searchbar';
import Notifications from './notification';
import ThemeSwitcher from './themeswitcher';
import LanguageSelector from './langauge';
import HelpIcon from './helpIcon';
import UserProfile from './user';

export default function Header() {
    return (
        <div className="w-full bg-white shadow-md py-3 px-6 flex items-center justify-between">
            {/* Logo or Brand Name */}
            <h1 className="text-xl font-semibold text-gray-800 hidden md:block">Nexus</h1>


            {/* Icons and Admin Info */}
            <div className="flex items-center space-x-6 ml-auto">
                <Notifications />
                <ThemeSwitcher />
                <LanguageSelector />
                <HelpIcon />
                <UserProfile />
            </div>
        </div>
    );
}
